import {
  LOAD_ANS,
  LOAD_CLEAR,
  LOAD_BUTTONS,
  LOAD_BACKSPACE,
} from "./calculator.actionType";

export const CALCULATOR_KEY = "calculatorStore";

let initialState = {
  number: "",
  answer: "",
};

export const calculatorReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case LOAD_BUTTONS:
      return {
        ...state,
        ...payload,
        number: state.number.concat(payload),
      };

    case LOAD_ANS:
      try {
        return {
          ...state,
          ...payload,
          answer: eval(state.number).toString(),
        };
      } catch (error) {
        return {
          ...state,
          ...payload,
          answer: "Error",
        };
      }

    case LOAD_CLEAR:
      return {
        ...state,
        ...payload,
        number: "",
        answer: "",
      };

    case LOAD_BACKSPACE:
      return {
        ...state,
        ...payload,
        number: state.number.slice(0, -1),
        answer: "",
      };

    default:
      return state;
  }
};
